.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1140px; } }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px; }

ul {
  list-style-type: none;
  padding-left: 0; }

.clearfix:after {
  content: '';
  display: table;
  clear: both; }

img {
  max-width: 100%;
  height: auto; }

.header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  color: #FFFFFF;
  background-color: rgba(0, 0, 0, 0.8); }
  .header .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between; }
  .header h1 {
    font-size: 1.1em; }
  .header .menu {
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    margin: 0;
    max-height: 0px;
    overflow: hidden;
    transition: max-height 1.5s;
    background: rgba(0, 0, 0, 0.8);
    z-index: 5; }
    .header .menu a {
      display: block;
      padding: 1em;
      color: #FFFFFF;
      text-decoration: none; }
      .header .menu a:active {
        color: #FFFFFF;
        background-color: orange; }
  .header .toggle-menu {
    color: #FFFFFF;
    font-size: 24px; }
  @media (min-width: 768px) {
    .header .menu {
      display: flex;
      position: static;
      max-height: none;
      background: transparent; }
      .header .menu a:hover {
        color: orange;
        background-color: transparent;
        box-shadow: 0 -2px 0 orange inset; }
    .header .toggle-menu {
      display: none; } }

.menu-show .menu {
  max-height: 300px; }

.about {
  padding-top: 56px; }
  .about .about-me {
    padding: 1em;
    line-height: 2;
    text-align: center;
    font-size: 18px; }

.main-title {
  font-size: 3em;
  text-align: center;
  position: relative; }
  .main-title:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: -10px;
    margin: auto;
    width: 2em;
    border-bottom: 5px solid orange; }

.skill {
  width: 100%;
  margin: 1em 0;
  padding-right: 15px;
  padding-left: 15px;
  text-align: center; }
  .skill .skill-list {
    height: 100%;
    border: 1px solid #dbdbdb;
    box-shadow: 0px 3px 5px #dbdbdb;
    border-radius: 0.5em; }
  .skill img {
    margin-top: 1em; }
  .skill .skill-list-content {
    list-style-type: disc;
    text-align: left; }
    .skill .skill-list-content li {
      margin-bottom: 0.5em;
      padding: 0 1em;
      font-size: 18px;
      list-style-position: inside; }
  @media (min-width: 768px) {
    .skill {
      flex: 0 0 33.333333%;
      max-width: 33.333333%; } }

.project {
  width: 100%;
  margin: 1em 0;
  padding-right: 15px;
  padding-left: 15px;
  text-align: center; }
  .project img {
    margin-top: 0.5em; }
  .project .project-content {
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    color: #ffffff;
    z-index: 5; }
    .project .project-content h3 {
      font-size: 1.5em;
      margin: 0; }
    .project .project-content p {
      margin: 0.5em 0 0 0; }
  .project .project_link {
    opacity: 0;
    position: absolute;
    bottom: 10px;
    right: 10px;
    z-index: 5;
    display: block;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    line-height: 35px;
    font-size: 16px;
    color: orange;
    text-align: center;
    background: #FFF; }
  @media (min-width: 992px) {
    .project {
      flex: 0 0 33.333333%;
      max-width: 33.333333%; } }

.project-list {
  position: relative;
  overflow: hidden;
  height: 100%;
  border: 1px solid #dbdbdb;
  box-shadow: 0px 3px 5px #dbdbdb;
  border-radius: 0.5em; }
  .project-list:after {
    content: "";
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    transition: all 0.4s ease; }
  .project-list:hover:after {
    opacity: 1; }
  .project-list:hover .project-content {
    opacity: 1; }
  .project-list:hover .project_link {
    opacity: 1; }

.contact {
  padding-bottom: 1em;
  text-align: center; }
  .contact .my {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 2px solid orange; }
  .contact .mail {
    margin-right: 5px; }

.footer {
  background: rgba(0, 0, 0, 0.8); }
  .footer p {
    padding: 1.5em;
    text-align: center;
    color: #fff; }
